@use "../../assets/styles/variables";

.card__slider {
  position: relative;
  overflow: hidden;
  margin-bottom: 60px;
}

.card__list {
  position: relative;
  overflow: hidden;
  display: grid;
  justify-items: center;
  //grid-template-columns: repeat(5, 1fr);
  grid-gap: 20px 20px;
  height: 280px;
  margin-bottom: 20px;
}

.card__item {
  display: grid;
  position: absolute;
  height: 280px;
  max-width: 240px;
  width: 100%;
  align-items: end;
  padding: 40px 20px;
  border-radius: variables.$borderRadiusCard;
  background-color: variables.$colorLightGrey;
  text-align: center;
  z-index: 5;


  //animation
  animation-name: spin;
  animation-duration: 200ms;
  animation-iteration-count: 1;
  @keyframes spin {
    0% {
      transform: rotateY(0deg);
    }
    100% {
      transform: rotateY(-180deg);
    }
  }

  & > * {
    animation-name: spin-visibility;
    animation-duration: 200ms;
    animation-iteration-count: 1;
    @keyframes spin-visibility {
      0% {
        transform: rotateY(0deg);
        opacity: 0;
      }
      100% {
        transform: rotateY(-180deg);
        opacity: 1;
      }
    }
  }


}


.card__word {
  margin-bottom: 5px;
  font-size: 25px;
  font-weight: bold;
  //transform: variables.$transformRotate;
}

.card__transcription {
  color: variables.$colorDarkGrey;
  //transform: variables.$transformRotate;
}

.card__btn-wrap {
  position: relative;
  //transform: variables.$transformRotate;
  .btn {
    position: relative;
    width: 100%;
    margin: 0 auto;
    z-index: 3;

    &.js_hidden {
      opacity: 0;
      z-index: -1;
    }
  }
}

.card__translation {
  display: block;
  padding: 8px 0;
  text-align: center;
}

.card__arrows {
  display: block;
  position: absolute;
  width: 100%;
  top: 120px;

  button {
    position: absolute;
    top: 0;

    &.js_hidden {
      display: none;
    }

    &:before {
      content: "";
      display: block;
      position: relative;
      width: 24px;
      height: 24px;
      background: url("../../assets/images/icons/arrow.svg");
    }

    &.arrows__prev {
      right: calc(50% + 200px);

      &:before {
        transform: rotate(180deg);
      }
    }

    &.arrows__next {
      left: calc(50% + 200px);
    }
  }
}

.card__counter {
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}

.card__stydied-word {
  font-size: 20px;
  text-align: center;
  span {
    font-weight: bold;
  }
}

.card__finish {
  text-align: center;
  h2 {
    margin-bottom: 10px;
    line-height: 1;
    font-size: 40px;
    font-weight: bold;
  }
  p {
    font-size: 30px;
  }
}